import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [{
  path: '',
  redirectTo: 'home',
  pathMatch: 'full'
}, {
  path: 'home',
  loadChildren: () => import('./modules/home/home.module').then((module) => module.HomeModule)
}, {
  path: 'about',
  loadChildren: () => import('./modules/about/about.module').then((module) => module.AboutModule)
}, {
  path: 'portfolio',
  loadChildren: () => import('./modules/portfolio/portfolio.module').then((module) => module.PortfolioModule)
}, {
  path: 'contact',
  loadChildren: () => import('./modules/contact/contact.module').then((module) => module.ContactModule)
}, {
  path: 'resume',
  loadChildren: () => import('./modules/resume/resume.module').then((module) => module.ResumeModule)
}];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
