import { EntityState, EntityAdapter, createEntityAdapter } from '@ngrx/entity';

export interface StringRequest {
  id: string;
}

export interface IApp extends EntityState<StringRequest> {
  itIsHome: boolean;
  activeLang: string;
  error: any;
}

export const adapter: EntityAdapter<StringRequest> = createEntityAdapter<StringRequest>();

export const InitialAppState: IApp = adapter.getInitialState({
  itIsHome: false,
  activeLang: 'es',
  error: null
});
