import { Component, OnInit, HostListener, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { AppFacade } from '../store/app.facade';

@Component({
  selector: 'app-translation',
  templateUrl: './translation.component.html',
  styleUrls: ['./translation.component.scss']
})
export class TranslationComponent implements OnInit, OnChanges {
  @Input() darkVersion = false;
  @Input() close = false;
  @Output() itsOpen = new EventEmitter();

  opened = false;
  private activeLang = localStorage.getItem('activeLang') || 'es';

  @HostListener('document:click')
  clickOutside() {
    this.opened = false;
  }

  constructor(public appFacade: AppFacade, private translate: TranslateService) {
    this.translate.setDefaultLang(this.activeLang);
    this.translate.use(this.activeLang);
    this.appFacade.setActiveLang(this.activeLang);
  }

  ngOnInit() {
    if (!localStorage.getItem('activeLang')) {
      localStorage.setItem('activeLang', this.activeLang);
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.close && changes.close.currentValue) {
      this.opened = false;
    }
  }

  toggle(event: Event) {
    event.stopPropagation();
    this.opened = !this.opened;
    this.itsOpen.emit();
  }

  change(lang: string) {
    this.opened = false;
    this.updateTranslateService(lang);
    localStorage.setItem('activeLang', lang);
  }

  private updateTranslateService(lang: string) {
    this.translate.setDefaultLang(lang);
    this.translate.use(lang);
    // redux
    this.appFacade.setActiveLang(lang);
  }
}
