import { createFeatureSelector, createSelector } from '@ngrx/store';
import { IApp } from './app.models';
import { APP_FEATURE_KEY } from './app.reducer';

export const selectAppState = createFeatureSelector<IApp>(APP_FEATURE_KEY);
export const getItIsHome = createSelector(selectAppState, (state: IApp) => state.itIsHome);
export const getActiveLang = createSelector(selectAppState, (state: IApp) => state.activeLang);
