import { Component, AfterViewInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { AppFacade } from 'src/app/store/app.facade';
import { SwiperComponent } from '../../../swiper/swiper.component';
import { CONSTANTS } from './portfolio.constants';

export interface Project {
  name: string;
  description: string;
  technical: string[];
  cover: string;
  images: string[];
}

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.scss'],
})
export class PortfolioComponent implements AfterViewInit {
  myToggle = false;
  projects = CONSTANTS.projects.slice(0, 6);

  constructor(
    public dialog: MatDialog,
    private appFacade: AppFacade,
  ) {
  }

  ngAfterViewInit(): void {
    this.appFacade.setItIsHome(false);
  }

  openDialog(project: Project): void {
    const dialogRef = this.dialog.open(SwiperComponent, {
      width: '95%',
      height: 'calc(95% - 35px)',
      data: project,
      disableClose: true,
      panelClass: 'swiper-dialog-container'
    });
    dialogRef.afterOpened().subscribe(() => {
      this.appFacade.sendAnalyticsEvent('view_detail', 'portfolio', project.name, 'click');
    });
  }

  viewAll(status: boolean): void {
    this.myToggle = status;
    if (this.myToggle) {
      this.projects = CONSTANTS.projects;
      this.appFacade.sendAnalyticsEvent('view_all', 'portfolio', 'portfolio', 'click');
    } else {
      this.projects = CONSTANTS.projects.slice(0, 6);
      this.appFacade.sendAnalyticsEvent('view_less', 'portfolio', 'portfolio', 'click');
    }
  }
}
