import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { AppFacade } from 'src/app/store/app.facade';
import { CONSTANTS } from './about.constant';

interface About {
  text: string;
  percent: number;
}

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.scss']
})
export class AboutComponent implements OnInit {
  searchForm: FormGroup;
  myToggle: boolean;
  skills = CONSTANTS.SKILLS.slice(0, 6);
  viewMode: string;

  constructor(
    private fb: FormBuilder,
    private appFacade: AppFacade
  ) { }

  ngOnInit(): void {
    this.initForm();
    this.appFacade.setItIsHome(false);
    this.viewMode = localStorage.getItem('viewMode') || 'grid';
  }

  filter(searchText: string): void {
    if (searchText) {
      this.skills = CONSTANTS.SKILLS.filter((skill: About) => skill.text.toLowerCase().indexOf(searchText.toLowerCase()) !== -1);
      this.appFacade.sendAnalyticsEvent('search_skill', 'about', searchText, 'click');
    } else {
      this.viewAll(this.myToggle);
    }
  }

  clearSearch(): void {
    this.searchForm.reset();
    this.viewAll(this.myToggle);
  }

  viewAll(status: boolean): void {
    this.myToggle = status;
    if (this.myToggle) {
      this.skills = CONSTANTS.SKILLS;
      this.appFacade.sendAnalyticsEvent('view_all_skills', 'about', 'skills', 'click');
    } else {
      this.skills = CONSTANTS.SKILLS.slice(0, 6);
      this.appFacade.sendAnalyticsEvent('view_less_skills', 'about', 'skills', 'click');
    }
  }

  setViewMode(mode: string): void {
    this.viewMode = mode;
    localStorage.setItem('viewMode', mode);
  }

  private initForm(): void {
    this.searchForm = this.fb.group({
      searchText: ['', Validators.compose([Validators.required])]
    });
  }
}
