import { createReducer, on, Action } from '@ngrx/store';
import { setActiveLang, setItIsHome, setItIsHomeFail, setItIsHomeSuccess } from './app.actions';
import { InitialAppState, IApp } from './app.models';

export const APP_FEATURE_KEY = 'AppState';

export const reducer = createReducer(
  InitialAppState as IApp,
  on(setItIsHome, (state, { itIsHome }) => ({
    ...state,
    itIsHome
  })),
  on(setItIsHomeSuccess, (state) => ({
    ...state,
    error: null
  })),
  on(setItIsHomeFail, (state, { error }) => ({
    ...state,
    error
  })),
  on(setActiveLang, (state, { activeLang }) => ({
    ...state,
    activeLang
  })),
);

export function AppReducer(state: IApp, action: Action): IApp {
  return reducer(state as IApp, action as Action);
}
