import { Injectable } from '@angular/core';
import { select, Store } from '@ngrx/store';
import { sendAnalyticsEvent, setActiveLang, setItIsHome } from './app.actions';
import { IApp } from './app.models';
import { getActiveLang, getItIsHome } from './app.selectors';

@Injectable()
export class AppFacade {
  constructor(private store: Store<IApp>) { }

  itIsHome$ = this.store.pipe(select(getItIsHome));
  activeLang$ = this.store.pipe(select(getActiveLang));

  setItIsHome(itIsHome: boolean) {
    this.store.dispatch(setItIsHome({ itIsHome }));
  }

  sendAnalyticsEvent(eventName: string, eventCategory: string, eventLabel: string, eventAction: string = null, eventValue: number = null) {
    this.store.dispatch(sendAnalyticsEvent({ eventName, eventCategory, eventLabel, eventAction, eventValue }));
  }

  setActiveLang(activeLang: string) {
    this.store.dispatch(setActiveLang({ activeLang }));
  }
}
