import { Component, OnInit, OnDestroy, AfterViewInit, ElementRef, Inject } from '@angular/core';
import { Swiper, SwiperOptions } from 'swiper';
import SwiperCore, { Zoom, Navigation, Pagination, Scrollbar, A11y, Autoplay } from 'swiper/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-swiper',
  templateUrl: './swiper.component.html',
  styleUrls: ['./swiper.component.scss']
})
export class SwiperComponent implements OnInit, OnDestroy, AfterViewInit {
  swiper: Swiper | undefined;

  constructor(
    private elementRef: ElementRef,
    private dialogRef: MatDialogRef<SwiperComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any
  ) {
    SwiperCore.use([Zoom, Navigation, Pagination, Scrollbar, A11y, Autoplay]);
  }

  ngOnInit(): void { }

  ngOnDestroy(): void {
    this.swiper = undefined;
  }

  ngAfterViewInit(): void {
    const swiperParams: SwiperOptions = {
      loop: true,
      autoplay: { delay: 15000 },
      zoom: true,
      pagination: {
        el: '.swiper-pagination',
        type: 'fraction',
        clickable: true
      },
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      }
    };

    this.swiper = new Swiper(this.elementRef.nativeElement.querySelector('.swiper-container'), swiperParams);
    this.mouseEnterListener();
    this.mouseLeaveListener();
  }

  close(): void {
    this.dialogRef.close();
  }

  private mouseEnterListener() {
    const swiper = this.swiper;
    if (swiper?.$el?.length) {
      const $el = swiper.$el[0];
      $el.addEventListener(
        'mouseenter',
        () => {
          swiper.autoplay.stop();
        },
        false
      );
    }
  }

  private mouseLeaveListener() {
    const swiper = this.swiper;
    if (swiper?.$el?.length) {
      const $el = swiper.$el[0];
      $el.addEventListener(
        'mouseleave',
        () => {
          swiper.autoplay.start();
        },
        false
      );
    }
  }
}
