import { Injectable } from "@angular/core";
import { Actions, createEffect, ofType } from "@ngrx/effects";
import { switchMap } from "rxjs/operators";
import { sendAnalyticsEvent } from "./app.actions";
import { AppService } from "./app.service";


@Injectable()
export class AppEffects {

    constructor(private actions$: Actions, private appService: AppService) { }

    sendAnalyticsEvent$ = createEffect(() =>
        this.actions$.pipe(
            ofType(sendAnalyticsEvent),
            switchMap(action => this.appService.sendAnalyticsEvent(action.eventName, action.eventCategory, action.eventLabel, action.eventAction, action.eventValue))
        ), { dispatch: false }
    )
}