import { ChangeDetectorRef, Component, OnInit, AfterContentChecked, HostListener } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router, RouterEvent } from '@angular/router';
import { SwUpdate } from '@angular/service-worker';
import { TranslateService } from '@ngx-translate/core';
import { filter } from 'rxjs/operators';
import { ModalConfirmationComponent } from './modal-confirmation/modal-confirmation.component';
import { AppFacade } from './store/app.facade';

declare let gtag: (...args: any) => void;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, AfterContentChecked {
  title = 'Jeison Nisperuza';
  itIsHome = false;
  isMenuOpened = false;
  currentYear = new Date().getFullYear();

  constructor(
    public appFacade: AppFacade,
    private router: Router,
    private ref: ChangeDetectorRef,
    private dialog: MatDialog,
    private swUpdate: SwUpdate,
    private translate: TranslateService
  ) { }

  @HostListener('document:click')
  clickOutside() {
    this.isMenuOpened = false;
  }

  ngOnInit(): void {
    // Google analytics
    this.router.events.pipe(
      filter(e => e instanceof RouterEvent)
    ).subscribe((e: RouterEvent) => {
      gtag('config', 'UA-186099970-1', {
        // eslint-disable-next-line @typescript-eslint/naming-convention
        page_path: e.url
      });
    });
    // Verify update
    if (this.swUpdate.isEnabled) {
      this.swUpdate.available.subscribe(() => {
        this.showUpdateMessage();
      });
    }
  }

  /**
   * @description To ignore ExpressionChangedAfterItHasBeenCheckedError
   */
  ngAfterContentChecked(): void {
    this.ref.detectChanges();
  }

  toggleMenu(event: Event): void {
    event.stopPropagation();
    this.isMenuOpened = !this.isMenuOpened;
  }

  private showUpdateMessage() {
    const dialogRef = this.dialog.open(ModalConfirmationComponent, {
      data: {
        type: 'SUCCESS',
        title: this.translate.instant('GENERAL.update'),
        message: this.translate.instant('APP.message_update_software')
      }
    });
    dialogRef.afterClosed().subscribe(() => {
      window.location.reload();
    });
  }
}
