import { createAction, props } from '@ngrx/store';

export const setItIsHome = createAction('[AppModule] Set itIsHome Action', props<{ itIsHome: boolean }>());
export const setItIsHomeSuccess = createAction('[AppModule] Set itIsHome Success Action');
export const setItIsHomeFail = createAction('[AppModule] Set itIsHome Fail Action', props<{ error: any }>());
export const sendAnalyticsEvent = createAction('[AppModule] Send Analytics Event',
    props<{
        eventName: string;
        eventCategory: string;
        eventAction: string;
        eventLabel: string;
        eventValue: number;
    }>()
);
export const setActiveLang = createAction('[AppModule] Set Active Lang Action', props<{ activeLang: string }>());
